
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
name:"edit",
props:['id'],
components:{
XButton,
XTextarea,
Group,
mavonEditor
},
data(){
return{
topics:{
title:"",
content:"",
toolbars: {
bold: true, // 粗体
italic: true, // 斜体
header: true, // 标题
underline: true, // 下划线
strikethrough: true, // 中划线
mark: true, // 标记
superscript: true, // 上角标
subscript: true, // 下角标
quote: true, // 引用
ol: true, // 有序列表
ul: true, // 无序列表
link: true, // 链接
imagelink: true, // 图片链接
code: true, // code
table: true, // 表格
fullscreen: true, // 全屏编辑
readmodel: true, // 沉浸式阅读
htmlcode: true, // 展示html源码
help: true, // 帮助
/* 1.3.5 */
undo: true, // 上一步
redo: true, // 下一步
trash: true, // 清空
save: true, // 保存（触发events中的save事件）
/* 1.4.2 */
navigation: true, // 导航目录
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */
subfield: true, // 单双栏模式
preview: true, // 预览
}
}
}
},
methods:{
editTopic(){
var app=this;
this.$http.post("/topics/update",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic_id,
tab:"dev",
title:this.topics.title,
content:this.topics.content
}).then(function (res) {
console.log(res.data);
if (res.data.success){
app.$router.back();
}
})
}
},
beforeRouteEnter(to,from,next){
next(function (vm) {
vm.$http.get("/topic/"+to.params.id,{
params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function (res) {
if (res.data.success){
vm.topics=res.data.data;
}
})
})
},
beforeRouteUpdate(to,from,next){
var app=this;
this.$http.get("/topic/"+to.params.id,{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function (res) {
if (res.data.success){
app.topics=res.data.data;
}
})
next();
}
}



