import Vue from "vue";
import Vuex from "vuex";
import mutations from "./mutations";
import actions from "./actions";
Vue.use(Vuex);
const store=new Vuex.Store({
  state:{
    topiclist:[
      { name:"all",desc:"全部"},
      { name:"good",desc:"精华"},
      { name:"share",desc:"分享"},
      { name:"ask",desc:"问答"},
      { name:"job",desc:"招聘",},
      { name:"dev",desc:"测试",},
    ],
    issCon:false,   //默认不显示actionsheet弹出框  显示更多菜单
    isLogined:false,  //默认为登录状态
    //登录后的菜单
    loginedMenus: {
      create:"发表新话题",
      center:"个人中心",
      msgs:"消息中心",
      logout:"退出",
    },
    nulogunMenus:{
      login:"登录"
    },
    loginname:"",//用来保存登陆后的用户名
    author_id:"",//保存登陆用户的id
    accesstoken:"",//用来保存登陆后的访问令牌
    msgCount:"",//表示未读消息数量
  },
  //状态数据的计算属性
  getters:{
    //根据登录状态显示不同的更多菜单
    moreMenus(state){
      if (state.isLogined){    //判断用户是否登陆
        return state.loginedMenus   //登陆后显示登陆后的菜单
      }
      return state.nulogunMenus    //没有登陆后显示没有登陆的菜单
    },
    //处理分类显示汉字
    fileterTab(state){
      return function (tab) {
        for (let i=0;i<state.topiclists.length;i++){
          const  element=state.topiclists[i];
          if (element.name == tab){
            return state.topiclists[i].desc;
          }
        }
      }
    }
  },
  mutations,
  actions
});
export default store;



