
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import {mapActions} from "vuex";
export default {
name: "msgs",
components:{
ButtonTab,
ButtonTabItem,
Cell,
Group,
Swipeout,
SwipeoutItem,
SwipeoutButton,
},
data(){
return{
selectedIndex:0,
hasReadMsgs:"",
hasNotReadMsgs:"",
}
},
methods:{
...mapActions(['getMsgsCount']),
getReadMsgs(){
var app=this;
this.$http.get("/messages",{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function (res) {
console.log(res.data);
if (res.data && res.data.success){
app.hasReadMsgs=res.data.data.has_read_message;
app.hasNotReadMsgs=res.data.data.hasnot_read_message;
}
})
},

handleTeb(index){
this.selectedIndex=index;
},

markRead(mark_id){
var app=this;
this.$http.post("/message/mark_one/"+mark_id,{
accesstoken:this.$store.state.accesstoken
}).then(function (res) {
console.log(res.data);
if (res.data&&res.data.success){
app.getMsgsCount();
console.log("标记为已读成功");



var markMsgs;
for(var i=0;i<app.hasNotReadMsgs.length;i++){
if (app.hasNotReadMsgs[i].id==mark_id){
markMsgs=app.hasNotReadMsgs[i];
app.hasNotReadMsgs.splice(i,1);
break;
}
}
app.hasReadMsgs.unshift(markMsgs);
}
})
},

markAllRead(){
var app=this;
this.$http.post("/message/mark_all",{
accesstoken:this.$store.state.accesstoken
}).then(function (res) {
console.log(res.data);
if (res.data && res.data.success){
app.$router.go(0);
}
})
},
},
created() {
this.getReadMsgs();
},
}



